body {
  height: 100%;
  margin: 0;
  display: grid;
  grid-template-columns: auto 30%;
  font-family: "Montserrat";
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

.line {
  position: absolute;
}

main {
  margin-left: 20%;
  height: 100vh;
  display: grid;
  place-content: center;

  .content {
    width: 70%;

    h1 {
      font-size: 3em;
      position: relative;

      span {
        display: block;
      }
    }

    p {
      font-size: 1.2em;
      width: 80%;
      line-height: 1.7em;
      margin-bottom: 2em;
    }
  }
}

aside {
  background: url("https://s3-us-west-2.amazonaws.com/s.cdpn.io/2621168/windows.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  height: 100vh;
}

img {
  width: 34%;
  position: absolute;
  right: 15%;
  top: 20%;
}

a {
  display: inline-block;
  background: #b100f2;
  color: white;
  font-weight: bold;
  text-transform: uppercase;
  text-decoration: none;
  padding: 1em;
}

span:after {
  content: " ";
  position: absolute;
  left: 0;
  width: 100%;
  height: 100%;
  // background: white;
  z-index: 1;
  transform-origin: 100% 0;
}
